import './Post.module.css';
import s from './Post.module.css';

type PropsType = {
    message: string
    likesCount: number
}

const Post: React.FC<PropsType> = (props) => {
    return (
        <div className={s.item}>
            <img
                src="data:image/jpeg;base64,"
                alt="avatar"/>
            { props.message }
            <div>
                <span>Like</span> {props.likesCount}
            </div>
        </div>

    )
}

export default Post;